<script setup>
import {onMounted, ref} from "vue"
import {store} from "@/store";
import {CommentOutlined, LeftOutlined, LikeOutlined, ReadOutlined, RollbackOutlined} from "@ant-design/icons-vue"
import requestService from "@/utils/request";
import {setLocalStorageItem} from "@/utils/data";
import router from "@/router";
import {notification} from "ant-design-vue";
import {onBeforeRouteUpdate} from "vue-router";

const token = ref("")

const followers = ref([])
const followings = ref([])

const followersCount = ref(0);
const followingsCount = ref(0);

const userId = ref("")

const getFollowsCount = () => {
  followersCount.value = store.followers.length;
  followingsCount.value = store.followings.length;
}

const toFeedDetail = (feedId) => {
  router.push({path: '/postDetail', query: {feedId: feedId}})
}

const btnCase = ref('1') // 1:关注 2:互相关注 3:已关注

onMounted(() => {
  userId.value = router.currentRoute.value.query.userId;
  getUserInfo(userId.value)
  getFollowsCount();
  getFeed();
})

onBeforeRouteUpdate((to) => {
  userId.value = to.query.userId;
  getUserInfo(userId.value)
  getFollowsCount();
  getFeed();
});

const getUserInfo = (uid) => {
  requestService.get("/user/" + uid).then((res) => {
    if (res.data.code === 2001) {
      userInfo.value = res.data.data.userInfo;
      followers.value = res.data.data.followers;
      followings.value = res.data.data.followings;
      followersCount.value = res.data.data.followers.length;
      followingsCount.value = res.data.data.followings.length;


      if (store.followers.some(follower => follower.follower === userId.value) && store.followings.some(following => following.following === userId.value)) {
        btnCase.value = '2'
      } else if (store.followings.some(following => following.following === userId.value)) {
        btnCase.value = '3'
      } else {
        btnCase.value = '1'
      }
    }
  })
}

const ellipsis = ref(true);

const iconColor = ref("#000")

const changeColor = () => {
  iconColor.value = iconColor.value === '#000' ? "#de1c31" : "#000"
}

const getFeed = () => {
  requestService.get("/feed/getFeedByUserId/" + userId.value).then((res) => {
    if (res.data.code === 2001) {
      feed.value = res.data.data;
    }
  })
}

const userInfo = ref({
  userName: "",
  userId: "",
  nickName: "",
  avatar: "",
})

const feed = ref([]);

const loading = ref(true);

const likeFeed = (item) => {
  requestService.post("/like",
      {
        feedId: item.feedId,
        feedUserId: item.userId
      },
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res) => {
    if (res.data.code === 3001) {
      openNotificationWithIcon('success', res.data.msg)
      item.likedUserIds.push(store.userInfo.userId)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}
const unLikeFeed = (item) => {
  requestService.delete("/like",
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        data: {
          feedId: item.feedId,
          feedUserId: item.userId
        }
      }
  ).then((res) => {
    if (res.data.code === 5001) {
      openNotificationWithIcon('success', res.data.msg)
      // 将用户id从feed的likedUserIds中删除
      item.likedUserIds.splice(item.likedUserIds.indexOf(store.userInfo.userId), 1)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const openNotificationWithIcon = (type, title, description) => {
  notification[type]({
    message: title,
    description: description,
    duration: 2
  });
};

const changeText = (event, text) => {
    event.target.innerText = text
}

const onFollow = (followingId) => {
  requestService.post("/follow",
      {
        followingId: followingId
      },
      {
        headers: {"Content-Type": "application/x-www-form-urlencoded"},
      }
  ).then((res) => {
    if (res.data.code === 3001) {
      store.followings.push({following: followingId})
      requestService.get('/user/').then((result) => {
        if (result.data.code === 2001) {
          setLocalStorageItem("userInfo", result.data.data, 5)
          store.userInfo = result.data.data.userInfo
          store.followers = result.data.data.followers
          store.followings = result.data.data.followings
          store.isLogin = true
          getUserInfo(userId.value)
        }
      })
    }
  })
}
const unFollow = (followingId) => {
  requestService.delete("/follow",
      {
        headers: {"Content-Type": "application/x-www-form-urlencoded"},
        data: {followingId: followingId}
      }
  ).then((res) => {
    if (res.data.code === 5001) {
      requestService.get('/user/').then((result) => {
        if (result.data.code === 2001) {
          setLocalStorageItem("userInfo", result.data.data, 5)
          store.userInfo = result.data.data.userInfo
          store.followers = result.data.data.followers
          store.followings = result.data.data.followings
          store.isLogin = true
          getUserInfo(userId.value)
        }
      })
    }
  })
}

const back = () => {
  router.go(-1);
}
</script>

<template>


  <div class="user-display">
    <el-affix :offset="80">
      <div class="feed-nav">
        <a-button class="back-btn" @click="back">返回
          <template #icon>
            <LeftOutlined/>
          </template>
        </a-button>

        <span :style="{fontSize:'18px',marginLeft:'170px',borderRadius:'8px',backgroundColor:'#c2c2c2',padding:'5px 20px'}">{{ userInfo.nickName }}</span>

      </div>
    </el-affix>
    <div class="user-background">
      <el-image class="bg-image" :src="userInfo.bgImageUrl" fit="cover"/>
    </div>

    <a-avatar :style="{marginTop:'-30px',marginLeft:'20px'}" :src="userInfo.avatar" :size="100"/>

    <div class="nickname">{{ userInfo.nickName }}</div>

    <div class="msg">
      <div><span>粉丝&nbsp;&nbsp;</span>{{ followersCount }}</div>
      <div><span>关注&nbsp;&nbsp;</span>{{ followingsCount }}</div>
    </div>

    <a-button @mouseover="changeText($event,'取消关注')" @mouseout="changeText($event,'已关注')"
              @click="unFollow(userId)" class="follow-btn"
              v-if="btnCase === '3'">
      已关注
    </a-button>
    <a-button @mouseover="changeText($event,'取消关注')" @mouseout="changeText($event,'互相关注')"
              @click="unFollow(userId)" class="follow-btn" shape="round"
              v-if="btnCase === '2'">互相关注
    </a-button>
    <a-button @mouseover="changeText($event,'关注')" @mouseout="changeText($event,'关注')"
              @click="onFollow(userId)" class="follow-btn" shape="round"
              v-if="btnCase === '1'">
      关注
    </a-button>


    <div class="desc">
      <ReadOutlined class="desc-icon"/>
      <a-typography-text>
        <div class="desc-text">
          {{ userInfo.descrption }}
        </div>
      </a-typography-text>
    </div>
  </div>

  <div :style="{marginTop:'20px',letterSpacing:'1px'}">
    全部动态({{ feed.length }})
  </div>

  <div :style="{marginTop:'20px'}">
    <a-card v-for="(item,index) in feed" :key="index" class="post" size="default">
      <a-card-meta>
        <template #title>
          <div class="post-nickname">{{ item.userNickName }}</div>
        </template>

        <template #description>
          <div class="post-createtime">{{ item.createTime }}</div>
        </template>

        <template #avatar>
          <a-avatar :size="60" :src="item.userAvatar"></a-avatar>
        </template>
      </a-card-meta>

      <div class="post-content">
        <a-typography-paragraph :ellipsis="ellipsis ? { rows: 5, expandable: true } : false"
                                :style="{lineHeight: '25px',fontSize:'16px'}" :content="item.content">
          <template #ellipsisSymbol>
            <span :style="{color:'#de1c31'}">展开</span>
          </template>
        </a-typography-paragraph>
      </div>


      <a-image
          :src="item.imageUrl"
      />

      <template #actions>
        <a-button>
          <template #icon>
            <RollbackOutlined key="forward"/>
          </template>
        </a-button>

        <a-button>
          <template #icon>
            <CommentOutlined key="comment" @click="toFeedDetail(item.feedId)"/>
          </template>
          {{ item.comments.length }}
        </a-button>

        <a-button @click="unLikeFeed(item)" v-if="item.likedUserIds.includes(store.userInfo.userId)">
          <template #icon>
            <LikeOutlined style="color: #de1c31" key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>
        <a-button @click="likeFeed(item)" v-else>
          <template #icon>
            <LikeOutlined key="like"/>
          </template>
          {{ item.likedUserIds.length }}
        </a-button>

      </template>
    </a-card>

  </div>
</template>

<style scoped>

.user-display {
  margin-top: 20px;
  background-color: white;
  border-radius: 5px;
  height: fit-content;
  padding-bottom: 20px;
  width: 100%;
}

.user-background {
  width: 100%;
  height: 350px;
  background-color: #de1c31;

}

.bg-image {
  width: 100%;
  height: 100%;
}

.nickname {
  font-size: 18px;
  font-weight: bold;
  margin-left: 130px;
  margin-top: -60px;
}

.msg {
  display: flex;
  justify-content: space-between;
  margin-left: 130px;
  margin-top: 10px;
  width: 150px;
  height: 20px;
}

.msg div {
  float: left;
  margin-right: 10px;
}

.msg div span {
  color: #c2c2c2;
}

.desc {
  min-width: 600px;
  max-width: 600px;
  display: flex;
  justify-content: space-between;
  width: 600px;
  height: fit-content;
  max-height: 100px;
  margin-top: 25px;
  margin-left: 15px;
}

.desc-icon {
  color: rgb(131, 130, 130);
  font-size: 17px;
  margin-top: 3px;
}

.desc-text {
  font-size: 13px;
  color: rgb(131, 130, 130);
  margin-left: 20px;
  box-sizing: content-box;
}


.post {
  margin-bottom: 20px;
}

.post button {
  border: none;
  box-shadow: none;
}

.post button:hover {
  color: #de1c31;
}

/*消除按钮点击样式*/
:global(.ant-wave) {
  display: none !important;
}

.post-nickname {
  font-size: 19px;
  min-width: 100px;
}

.post-createtime {
  font-size: 15px;
}

.post-content {
  margin: 20px 0;
}

.follow-btn {
  position: absolute;
  background-color: white;
  color: #de1c31;
  box-shadow: none;
  border: 1px #de1c31 solid;
  margin-left: 532px;
  margin-top: -50px;
}

.follow-btn:hover {
  background-color: white;
  color: rgba(222, 28, 49, 0.48);
  border: 1px #de1c31 solid;
}

.feed-nav {
  border-bottom-radius: 5px;
  background-color: white;
  padding: 10px;
}


.back-btn{
  background-color: rgba(0,0,0,0);
  border: black;
  font-size: 16px;
  box-shadow: none;
}

.back-btn:hover{
  color:#de1c31;
}
</style>